#footer {
	font-size: 0.875rem;
	font-family: sans-serif;
	background-color: map-get($eigen-colours, navy);
	color: map-get($eigen-colours, light-grey);
	padding-bottom: 0;
	.footer-widget {
		display: inline-block;
		margin-top: 2rem;
		margin-right: 2rem;
		margin-bottom: 2rem;
		vertical-align: top;
		//color: map-get($eigen-colours, white);

		a {
			//color: map-get($eigen-colours, white);
			cursor: pointer;
			font-size: 0;
			display: inline-block;
			text-align: center;
			width: 46px;
			height: 46px;
			border-radius: 0.2rem;
			transition: all 0.2s ease-in-out;
			background-color: map-get($eigen-colours, grey);
			color: map-get($eigen-colours, navy);
			
			i { font-size: 2.5rem; line-height: 46px;} 
		}

		a:hover {
			background-color: white;
		}

		h2 {
			font-size: 0.875rem;
			font-weight: 600;
			//text-transform: uppercase;
			display: inline-block;
			border-bottom: 1px solid map-get($eigen-colours, light-grey);
		}

		.box {
			@include clearfix();
			
			display: block;
			padding: 0;
			margin-bottom: 0.5rem;
		}
	}

	.social-profiles {
	}

	.social-profiles-inner {
		display: block;

		
	}

	.our-contact-details-inner {
		
		.map-marker {
			display: inline-block;
			margin-right: 0.5rem;
			vertical-align: top;
		}
		address {
			display: inline-block;
			line-height: 1.1;
			vertical-align: top;
		}
		.phone, .email {
			vertical-align: middle;
			margin-right: 0.5rem;
		}

	}

	.facebook:hover {
		color: #3b5998;
	}

	.twitter:hover {
		color: #1da1f2;
	}
	.map-marker:hover {
		color: red;
	}

	.back-to-top {
		@include clearfix();
		text-align: center;

		a {
			transition: all 0.2s ease-in-out;
			background-color: map-get($eigen-colours, grey);
			color: map-get($eigen-colours, navy);
			font-size: 2.5rem;

			&:hover {
				background-color: map-get($eigen-colours, yellow);
			}
		}
	}

	.copyright {
		text-align: center;
		padding: 1rem 0;
		p {
			font-size: 0.75rem;
			color: white;
		}
		background-color: darken(map-get($eigen-colours, navy), 10%);
	}
}