.container {
	display: block;
	margin: 0 auto;
	width: 100%;
	padding-left: $gutters / 2;
	padding-right: $gutters / 2;

	@include breakpoint(small-device) {
		max-width: $small-device - $gutters;
	}

	@include breakpoint(medium-device) {
		max-width: $medium-device - $gutters;
	}

	@include breakpoint(large-device) {
		max-width: 90rem - $gutters;
	}
}

.section {
	@include clearfix();
	padding: 2rem 0;

	@include breakpoint(small-device) {
		padding: 4rem 0;
	}
}

.eigen-navy {
	color: white;
	background-color: map-get($eigen-colours, navy);
}

.eigen-blue {
	//background: linear-gradient(to right, map-get($eigen-colours, blue), map-get($eigen-colours, navy), map-get($eigen-colours, blue));
	background-color: map-get($eigen-colours, blue);
	color: map-get($eigen-colours, white);
	h2 {
		//text-shadow: 1px 1px 1px map-get($eigen-colours, navy);
		font-weight: lighter;
		margin: 0 18%;
	}
	p {
		color: map-get($eigen-colours, white);
	}
}

.eigen-green {
	background-color: map-get($eigen-colours, green);
	color: map-get($eigen-colours, white);
	//text-shadow: 1px 1px 1px map-get($eigen-colours, navy);
	h2 {
		font-weight: lighter;
		margin: 0 18%;
	}
	p {
		color: map-get($eigen-colours, white);
	}
}

.eigen-grey {
	background-color: map-get($eigen-colours, light-grey);
}

.eigen-yellow {
	color: white;
	background-color: map-get($eigen-colours, yellow)

}
.eigen-video, .narrow {
	margin: 0 auto;
	max-width: 800px;
}

.eigen-feature {
	text-align: center;
	p {
		font-size: 1.3rem;
	}

	h2 {
		font-size: 1.8rem;
		color: map-get($eigen-colours, blue);
		font-weight: lighter;
		margin-bottom: 2rem;
	}

	img {
		display: block;
		height: auto;
		max-width: 300px;
		margin: 0 auto 2rem auto;
	}

	.container {
		display: flex;
		flex-direction: column;
	}
	
	.content, .image {
		width: 100%;
		padding: 0 1rem 0 1rem;
	}

	.image {
		order: 1;
	}

	.content {
		order: 2;
	}

	@include breakpoint(small-device) {
		img {
			max-width: 400px;
		}
	}

	@include breakpoint(medium-device) {
		text-align: left;
		p {
			font-size: 1.5rem;
		}
		h2 {
			font-size: 2.5rem;
		}
		img {
			max-width: 450px;
		}
		.container {
			flex-direction: row;
			align-items: center;
		}

		.image, .content {
			order: initial;
		}
	}
}

.box {
	width: 100%;
	padding: 1rem ;
	margin-bottom: 1rem;

	@include breakpoint(medium-device) {
		margin-bottom: 0;
	}
}

//Hero type containers
.heroic {
	
	text-align: center;

	h1, h2, h3, p {
		display: inline-block;
	}
	h1 {
		font-size: 2rem;
		font-weight: 200;
	}
	h2 {
		font-size: 1.8rem;
		line-height: 1.3;
		margin-bottom: 2rem;
		font-weight: 400;
	}

	p {
		font-size: 1.3rem;
	}

	*:last-child {
		margin-bottom: 0;
	}
	@include breakpoint(small-device) {
		h1 {
			font-size: 4rem;
			font-weight: lighter;
		}

		h1 span {
			font-size: 3.2rem;
		}
		h2 {
			font-size: 1.5rem;
		}

		h2.medium {
			font-size: 2rem;
		}
	}
}

.has-bg-image .heroic {
	color: map-get($eigen-colours, white);
}

.vc {
	position:relative;
	top:50%;
	transform: translateY(-50%);
}

.link-list {
	position:relative;
	width: 50%;
	left:50%;
	transform: translateX(-50%);
	font-size: 2rem;
	line-height: 2;

	a {
		text-decoration: none;
		color: map-get($eigen-colours, dark-grey);
		cursor: pointer;
	}

	a:hover {
		color: map-get($eigen-colours, blue);
	}

	li:before {
		content:'';
		display: block;
		margin-right: 2rem;
		background-color: map-get($eigen-colours, blue);
		width: 2rem;
		height: 2rem;
		float: left;
		margin-top: 1rem;

	}
}

.fullScreenBg {
	position: relative;
	height: 100%;
	top: 0;
	width: 100%;
	background: url('/images/rig-background.jpg') no-repeat center center;
	background-size: cover;
}

.fullScreenBg.playing-video {
	transition: height 0.2s ease-out;
}

.playing-video .overlay {
	transition: background-color 0.2s ease-out;
	background-color: map-get($eigen-colours, yellow);
}

.overlay {
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	transition: background-color 0.2s ease-out;
	background-color: rgba(0, 0, 0, 0.25);
}

.eigen-feature-highlight {
	display: flex;
	
	flex-direction: column;
	max-width: 280px;
	margin: 0 auto;
	height: 100%;
	padding: 1rem;
	text-decoration: none;
	color:  map-get($eigen-colours, dark-grey);

	h2 {
		
		line-height: 1;
	}
}

.eigen-feature-highlight:hover {
	.icon-efficient-work {
		@extend .icon-efficient-work-white;
	}
	.icon-shared-awareness {
		@extend .icon-shared-awareness-white;
	}
	.icon-condition-based-maintenance {
		@extend .icon-condition-based-maintenance-white;
	}
	background-color: map-get($eigen-colours, navy);
	color: white;

	p {
		color: white;
	}
}

.eigen-clients-bg {
	padding-top: 150px;
	background: url('/images/eigen-clients.jpg') no-repeat top center;
	background-size: cover;

	@include breakpoint(small-device) {

	}

	@include breakpoint(medium-device) {
		padding-top: 280px;
	}
}

.our-clients {
	overflow-x: scroll;
}
.our-clients img {
	min-width: 800px;
}

.side-content {
	margin-top: 2rem;

	.box {
		padding: 0;
		text-align: center;
	}

	@include breakpoint(medium-device) {
		margin-top: 0;

		.box {
			text-align: left;
		}
	}
}

.eigen-feature-video {
	position: relative;
	height: 0;
	overflow: hidden;
	opacity: 0;
	visibility: hidden;
	z-index: -1;
}

.playing-video .eigen-feature-video {
	height: auto;
	overflow: visible;
	opacity: 1;
	visibility: visible;
	z-index: 1;
}

.section.world-map {
	background: url('/images/world-map.jpg') no-repeat center center;
	background-size: cover;

	ul {
		width: 100%;
		color: map-get($eigen-colours, white);
	}

	ul li {
		display: block;
		float: left;
		width: 100%;
		margin-bottom: 1rem;
		font-size: 1rem;
	}

	i {
		color: map-get($eigen-colours, yellow);
	}

	@include breakpoint(small-device) {
		ul li {
			font-size: 1.5rem;
			width: 50%;
		}
	}
}

