ul.eigen-list {
	padding-left: 2rem;

	li {
		position: relative;
	}

	li:before {
		display: block;
		position: absolute;
		top: 50%;
		left: -1rem;
		content: "";
		width: 0.4rem;
		height: 0.4rem;
		transform: translateY(-50%);
		background-color: map-get($eigen-colours, navy);
	}
}

ul.job-list {
	padding-left: 2rem;

	li {
		position: relative;
	}

	li:before {
		display: block;
		position: absolute;
		top: .7rem;
		left: -1rem;
		content:"";
		width: 0.4rem;
		height: 0.4rem;
		border-radius: .2rem;
		transform: translateY(-50%);
		background-color: map-get($eigen-colours, navy);
	}

	ul.job-list li:before {
		background-color: transparent;
		border: 1px solid map-get($eigen-colours, navy);
	}
}

.job-list.job-list__inline {
	li {
		display: inline-block;
		margin-right: 4rem;
	}

	li:before {
		border-radius: 0;
	}
}