a.btn-play {
	display: inline-block;
	cursor: pointer;
	padding: 0.5rem;
	font-size: 4rem;
	border: 1px solid #fff;
	margin-top: 1rem;
	color: white;
	transition: opacity 0.3s ease-in-out;
	opacity: 0;
}

a.btn-play:hover {
	background-color: rgba( 255, 255, 255, 0.5);
}

.sm {
	min-width: 125px;
	min-height: 15px;
	line-height: 1.8;
}

a.btn, .btn {
	font-size: 1rem;
	font-weight: 600;
	text-align: center;
	text-transform: uppercase;
	text-decoration: none;
	display: inline-block;
	position: relative;
	color: map-get($eigen-colours, navy);
	border: 2px solid map-get($eigen-colours, navy);
	border-radius: 1.5em;
	cursor: pointer;

	span {
		transition: all 0.2s ease-in-out;
		background-color: transparent;
	}	
}

.btn-inner {
	display: block;
	border-radius: 1.5em;
	margin: 2px;
	padding: 0 40px;
}

a.btn:hover, .btn:hover {
	
	color: white;

	span {
		transition: all 0.2s ease-in-out;
		background-color: map-get($eigen-colours, yellow);
	}
}