.showoff {
	font-size: 0;
	position: relative;
	background-color: map-get($eigen-colours, light-grey);
	ul {
		font-size: 0;
	}
	li {
		//position: relative;
		width:25vw;
		height: 25vw;
		vertical-align: top;
		display: inline-block;
		background-color: map-get($eigen-colours, yellow);
		//transition: height 0.3s ease-in-out;
	}

	li.active {
		//transition-delay: 10s;
		//transition: height 0.3s ease-in-out;
	}
}

.showoff .loader {
	display: none;
	position: absolute;
	width: 100%;
	height: 100%;
	z-index: 1000;
	background-color: map-get($eigen-colours, green);
	top: 0;
	left: 0;
}

.showoff .loading .loader {
	display: block;
}

.showoff li.active .showoff-thumb {
	transition: transform 0.2s ease-in-out;
	transform: scale(0.8);
}
.showoff-thumb {
	display: block;
	position: relative;
	transition: transform 0.2s ease-in-out;
	width: 100%;
	height: 100%;
	cursor: pointer;
}

.showoff-thumb img {
	height: 100%;
}

li:hover .showoff-thumb-hover {
	background-color: rgba(0, 0, 0, 0.7);
}
.showoff-thumb-hover {
	transition: all 0.2s ease-in-out;
	position: absolute;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.3);
}

li.active .showoff-thumb-hover {
	transition: all 0.2s ease-in-out;
	background-color: map-get($eigen-colours, navy);
}

.showoff-thumb-hover-style {

	p, i {
		opacity: 1;
		position: absolute;
		text-align: center;
		width: 100%;
		top: 50%;
		transform: translateY(-50%);
		color: white;
	}

	i {
		opacity: 0;
		font-size: 4rem;
	}
}

.showoff li.active .showoff-thumb-hover-style {
	i {
		opacity: 1;
	}

	p {
		opacity: 0;
	}
}

.showoff-detail {
	@include clearfix();
	position: absolute;
	height: 0;
	background-color: white;
	overflow: hidden;
	top: 0;
	width: 100%;
	left:0;
	font-size: 1rem;
	padding: 0;

	p {
		font-size: 14px;
	}
}

.showoff li.active .showoff-detail {
	height: auto;
	overflow: visible;
	padding: 30px;
}

.showoff-detail .showoff-detail-close {
	display: block;
	cursor: pointer;
	float: right;
	padding: 0.5rem;
	font-size: 2rem;

	&:hover {
		color: map-get($eigen-colours, yellow);
	}
}

.showoff-detail-wrap {
	@include clearfix();
	background-color: white;
	transition: opacity 0.3s ease-in-out;
	position: relative;
	padding-top: 30px;
	opacity: 0;
	height: 0;
}

.showoff li.active .showoff-detail-wrap {
	transition: opacity 0.3s ease-in-out;
	padding-top: 0;
	height: auto;
	opacity: 1;
}

.showoff-detail-left, .showoff-detail-right {
	width: 100%;
	padding: 0;
	img {
			margin-bottom: 1rem;
		}
}



@include breakpoint(medium-device) {
	.showoff-detail-left {
		width: 60%;
		padding-right: 15px;
		float: left;

		img {
			margin-top: 48px;
			margin-bottom: 0;
		}
	}

	.showoff-detail-right {
		width: 40%;
		padding-left: 15px;
		float:right;
	}
}