.services-page {
	article header {
		&:before {
			background-color: map-get($eigen-colours, yellow);
		}

		h1.eigen-logo {
			background-color: map-get($eigen-colours, navy);

			&:before {
				background-color: map-get($eigen-colours, yellow);
			}
		}
	}
}

.product-page, .careers-page {
	article header {
		&:before {
			background-color: map-get($eigen-colours, navy);
		}

		h1.eigen-logo {
			background-color: map-get($eigen-colours, yellow);

			&:before {
				background-color: map-get($eigen-colours, navy);
			}
		}
	}
}

.uses-page {
	article header, .header {
		&:before {
			background-color: map-get($eigen-colours, blue);
		}

		h1.eigen-logo {
			background-color: map-get($eigen-colours, green);

			&:before {
				background-color: map-get($eigen-colours, blue);
			}
		}
	}
}

.services-page, .product-page, .uses-page, .about-page, .careers-page {

	article header, .header {
		position: relative;
		height: 260px;
		
		&:before {
			content: " ";
			display:block;
			position: absolute;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
			
			opacity: 0.5;
		}

		h1 {
			font-size: 1.5rem;
			line-height: 1;
			font-weight: 600;
			text-transform: uppercase;
			margin-top: 0.5rem;
		}

		h1.eigen-logo {
			position: relative;
			z-index: 2;
			font-size: 1.5rem;
			line-height: 48px;
			font-weight: 600;
			text-transform: uppercase;
			display: inline-block;
			padding: 0 20px;

			&:before {
				@extend .icon-eigen-title;
				background-position: center center;
				width: 100px;
				height: 48px;
				z-index: 2;
				content: "";
				display: block;
				float: left;
				margin-left: -20px;
				padding: 0 1rem;
				margin-right: 20px;
				
			}
		}

		.container {
			height: 100%;
		}
	}
}

@include breakpoint(small-device) {
	.services-page, .product-page, .uses-page, .about-page, .careers-page {
		article header, .header {
			height: 400px;
		}

		article header h1 {
			font-size: 3.5rem;
		}

		article header h1.eigen-logo, .header h1.eigen-logo {
			font-size: 3.5rem;
			line-height: 84px;

			&:before {
				
				width: 200px;
				height: 84px;
			}
		}

	}
}