p , ul {
	font-size: 0.9375rem;
	line-height: 1.5;
	margin-bottom: 1rem;
	color: map-get($eigen-colours, dark-grey);

	&:last-child {
		margin-bottom: 0;
	}
}

.lead {
	font-size: 1.2rem;
	font-style: italic;
	font-weight: 400;
	color: darken(map-get($eigen-colours, dark-grey), 10%);
}

strong {
	font-weight: bold;
}