h1, .h1, h2, .h2, h3, .h3, h4, h5, h6 {
	font-weight: 400;
	margin-bottom: 1rem;
	line-height: 1.5;

	&:last-child {
		margin-bottom: 0;
	}
}

h1, .h1 { font-size: 2rem; }
h2, .h2 { font-size: 1.5em; }
h3, .h3 { font-size: 1.17em; }
h4 { font-size: 1.12em; }
h5 { font-size: .83em; }
h6 { font-size: .75em; }
