#climbing-the-mountain {
	position: relative;
	padding: 1rem;
	margin-bottom: 1rem;
	font-size: 0;
	text-align: center;
	//width: fit-content;
	background-color: map-get($eigen-colours, blue);
	background: url('/images/uses/climbing-the-mountain.jpg') no-repeat;
	background-size: cover;

	&:before {
		content: "";
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		background-color: rgba(0,0,0,0.3);
	}
}
#climbing-the-mountain .shape, #climbing-the-mountain .detail {
	position: relative;
	padding: 40px 2px 2px 2px;
	
	
	display: inline-block;
	vertical-align: top;

	h3 {
		font-weight: 600;
	}
	h3, p {
		
		font-size: 12px;
		
		margin: 0;
		padding: 0;
		line-height: 1.1;
		color: map-get($eigen-colours, white);
	}
}

#climbing-the-mountain .detail {
	width: 272px;
	height: 400px;
}

#climbing-the-mountain.narrow .detail {
	overflow: hidden;
	height: 100px;
}

#climbing-the-mountain.narrow .detail-section {
	display: block;
	position: absolute;
	top:0;
	width: 100%;
}

#climbing-the-mountain .detail-section:nth-child(1) {
	left: -100%;
	animation: slide-one 20s linear infinite;
}
#climbing-the-mountain .detail-section:nth-child(2) {
	left: -100%;
	animation: slide-two 20s linear infinite;
}
#climbing-the-mountain .detail-section:nth-child(3) {
	left: -100%;
	animation: slide-three 20s linear infinite;
}
#climbing-the-mountain .detail-section:nth-child(4) {
	left: -100%;
	animation: slide-four 20s linear infinite;
}


#climbing-the-mountain .detail-section:nth-child(1) {
	h3, p {
		animation: highlight-text-one 20s linear infinite;
	}
}

#climbing-the-mountain .detail-section:nth-child(2) {
	h3, p {
		animation: highlight-text-two 20s linear infinite;
	}
}

#climbing-the-mountain .detail-section:nth-child(3) {
	h3, p {
		animation: highlight-text-three 20s linear infinite;
	}
}

#climbing-the-mountain .detail-section:nth-child(4) {
	h3, p {
		animation: highlight-text-four 20s linear infinite;
	}
}

#climbing-the-mountain .shape {
	width: 360px;
	height: 400px;

	&:before {
			content:"";
			display: block;
			position: absolute;
			background-color: white;
			transform: rotate(25deg);
			left: 90px;
			top:-7px;
			width: 4px;
			height: 420px;
			
		}

	&:after {
		content:"";
		display: block;
		position:absolute;
		background-color: white;
		transform: rotate(-25deg);
		right: 90px;
		top: -7px;
		width: 4px;
		height: 420px;

	}

	.shape-section:nth-child(4) {
		width:100%;
		border-bottom: 4px solid #fff;
		h3,p { 
			animation: highlight-text-four 20s linear infinite; 
			
		}
	}
	.shape-section:nth-child(3) {
		width:80%;
		h3, p {
			animation: highlight-text-three 20s linear infinite; 
			
		}
	}
	.shape-section:nth-child(2) {
		width:60%;
		h3, p {
			animation: highlight-text-two 20s linear infinite; 
			
		}
	}
	.shape-section:nth-child(1) {
		width:40%;
		h3, p {animation: highlight-text-one 20s linear infinite; }
	}
}

#climbing-the-mountain .shape-section, #climbing-the-mountain .detail-section {
	//position: relative;
	height: 89px;
	margin: 0 auto;
}

#climbing-the-mountain .shape-section-inner, #climbing-the-mountain .detail-inner {
	position: relative;
	width: 65%;
	left: 50%;
	top:50%;
	transform: translate(-50%, -50%);
}

@keyframes highlight-text-four {
	0% { color: map-get($eigen-colours, white); }
	4% { color: map-get($eigen-colours, white); }
	5% { color: map-get($eigen-colours, yellow); }
	25% { color: map-get($eigen-colours, yellow); }
	26% { color: map-get($eigen-colours, white); }
}

@keyframes highlight-text-three {
	0% { color: map-get($eigen-colours, white); }
	24% { color: map-get($eigen-colours, white); }
	25% { color: map-get($eigen-colours, yellow); }
	50% { color: map-get($eigen-colours, yellow); }
	51% { color: map-get($eigen-colours, white); }
}

@keyframes highlight-text-two {
	0% { color: map-get($eigen-colours, white); }
	49% { color: map-get($eigen-colours, white); }
	50% { color: map-get($eigen-colours, yellow); }
	75% { color: map-get($eigen-colours, yellow); }
	76% { color: map-get($eigen-colours, white); }
}

@keyframes highlight-text-one {
	0% { color: map-get($eigen-colours, white); }
	74% { color: map-get($eigen-colours, white); }
	75% { color: map-get($eigen-colours, yellow); }
	100% { color: map-get($eigen-colours, yellow); }
}

@keyframes slide-four {
	0% { left: 0; }
	4% { left: 0; }
	5% { left: 0; }
	24% { left: 0; }
	25% { left: -100%; }
	100% { left: -100%; }

}

@keyframes slide-three {
	0% { left: -100%; }
	24% { left: -100%; }
	25% { left: 0; }
	49% { left: 0; }
	50% { left: -100%; }
}

@keyframes slide-two {
	0% { left: -100%; }
	49% { left: -100%; }
	50% { left: 0; }
	75% { left: 0; }
	76% { left: -100%; }
}

@keyframes slide-one {
	0% { left: -100% }
	75% { left: -100%; }
	76% { left: 0; }
	99% { left: 0; }
	100% { left: -100%; }
}


