
@include breakpoint(small-device) {
	.primary-nav + * {
		margin-top: 60px;
	}

	.has-bg-image .primary-nav + * {
		margin-top: 0;
	}
}

//General Logo
.primary-nav .header-logo {
	display: block;
	@extend .icon-eigen-logo;
	background-repeat: no-repeat;
	width: 120px;
	text-indent: -9999px;
	line-height: 46px;
	text-decoration: none;
}

.has-bg-image .primary-nav .header-logo {
	@extend .icon-eigen-logo-alt;
	background-repeat: no-repeat;
}

//Off canvas logo
.primary-nav .off-canvas {
	overflow-y: scroll;
	padding-top: 46px;
	@include clearfix();

	@include breakpoint(small-device) {
		overflow-y: visible;
	}
}

.primary-nav .off-canvas .header-logo {
	position: absolute;
	top: 7px;
	//float:left;
	//margin-top: 7px;
	@extend .icon-eigen-logo-alt;
	background-repeat: no-repeat;
}

.primary-nav .sub-navigation {
	display: none;
}

.primary-nav .sub-navigation.open {
	display: block;
}

//Close button

.close-menu {
	display: block;
	position: absolute;
	right: 10px;
	top: 0;
	cursor: pointer;
	margin-top: 7px;
	@extend .icon-close-menu;
	background-size: 30px 30px;
	background-position: center center;
	width: 46px;
	height:46px;
}

/*	color: map-get($eigen-colours, grey);
	transition: .3s;
	text-decoration: none;
}

.utils-wrapper a:hover {
	color: map-get($eigen-colours, blue);
}*/

.chevron {
	@extend .icon-chevron-down;
	cursor: pointer;
	background-repeat: no-repeat;
	background-position: top right;
	height: 29px;
	position: absolute;
	left:0;
	width: calc( 100% - #{$gutters / 2});
}

.primary-nav .off-canvas.open {
	display: block;
	left:0;
}


.primary-nav .main-menu a:hover {
	transition: .3s;
	color: map-get($eigen-colours, blue);
}
//Primary nav mobile closed
@media (max-width: 767px) {
	.primary-nav .off-canvas {
		position: fixed;
		top:0;
		left: -120%;
		width: 100%;
		height: 100%;
		z-index: 10000;
		background: map-get($eigen-colours, navy);
		transition: .5s;
	}
}

.has-bg-image .primary-nav .mobile-menu {
	position: absolute;
	z-index: 10000;
}
.primary-nav .mobile-menu {
	padding-top: 7px;
	height: 60px;
}

.primary-nav .mobile-menu .head-logo {
	width: calc( 100% - 46px);
	float:left;
}

.primary-nav .mobile-menu .mobile-menu-icon {
	@extend .icon-menu-white;
	width:46px;
	height: 46px;
	background-position: center right;
	cursor: pointer;
	float:right;
}

.primary-nav .main-menu {
	font-size: 1.5rem;
}

.primary-nav .main-menu .left {
	display: none;
}

.primary-nav .main-menu > ul {
	padding-top:20px;
	clear: both;
	overflow: hidden;
}

.primary-nav ul li {
	margin-top: 20px;
}

.primary-nav ul li ul {
	padding-left: 2rem;
}

.primary-nav ul li ul li {
	height: auto;
	min-height: 40px;
	font-size: 1.3rem;
	font-weight: lighter;
	margin:10px 0;
}
.primary-nav .drop-header {
	padding-left: 1.5rem;
	margin-top: 20px;
}

.primary-nav .drop-header {
	font-size: 0.85rem;
	font-weight: bold;
	color: map-get($eigen-colours, yellow);
}

.primary-nav .nav-tagline {
	h2 {
	font-size: 2.2rem;
	line-height: 1.5;
	color: map-get($eigen-colours, yellow);
	}
}

.primary-nav .main-menu a {
	text-decoration: none;
	color: map-get($eigen-colours, white);
}

@include breakpoint(small-device) {
	.primary-nav .sub-navigation.open {
		display: none;
	}
	.has-bg-image .primary-nav .header-logo,  {
		background: url('data:image/svg+xml;utf-8,#{map-get($svg-icons, eigen-logo)}');
		background-repeat: no-repeat;
		background-position: center center;
	}

	.has-bg-image .primary-nav .main-menu-wrapper.stick .header-logo, .has-bg-image .primary-nav:hover .off-canvas .header-logo {
		background: url('data:image/svg+xml;utf-8,#{map-get($svg-icons, eigen-logo)}');
		background-repeat: no-repeat;
		background-position: center center;
	}

	.has-bg-image .primary-nav .off-canvas.open .header-logo {
		margin-top: 0;
		background-position: center center;
	}

	.primary-nav .off-canvas .header-logo {
		background: url('data:image/svg+xml;utf-8,#{map-get($svg-icons, eigen-logo)}');
		background-repeat: no-repeat;
		background-position: center center;
	}

	.has-bg-image .primary-nav .off-canvas .header-logo {
		background: url('data:image/svg+xml;utf-8,#{map-get($svg-icons, eigen-logo-alt)}');
		background-repeat: no-repeat;
		background-position: center center;
	}

	.has-bg-image .main-menu-wrapper {
		background-color: transparent;
	}

	.main-menu-wrapper {
		@include clearfix();
		position: fixed; // relative
		width:100%; //
		background-color: transparent;
		order: 2;
		z-index: 100000;
		transition: .3s;
	}

	.has-bg-image .primary-nav:hover .main-menu-wrapper {
		border-bottom: 1px solid map-get($eigen-colours, grey);
		background-color: rgba(256, 256, 256, 0.8);
	}

	.main-menu-wrapper.stick {
		top: 0px;
		border-bottom: 1px solid map-get($eigen-colours, grey);
		background-color: rgba(256, 256, 256, 0.8);
		transition: .3s;
	}
	.utils-wrapper {
		margin-bottom: 60px;
		order: 1;
		height: 40px;

		.utils {
			float:right;
		}
	}

	.primary-nav .off-canvas {
		padding-top: 0;
	}
	.primary-nav .off-canvas .header-logo {
		top: 0;
	}

	.primary-nav .header-logo {
		background: url('data:image/svg+xml;utf-8,#{map-get($svg-icons, eigen-logo)}'); 
		margin-top: 0;
		line-height: 67px;
		background-position: center center;
		background-repeat: no-repeat;
	}

	.primary-nav .head-logo {
		width: 120px;
		float: left;
	}
	.primary-nav .main-menu {
		position: relative;
		@include clearfix();
		font-size: 1.3rem;
	}
	.primary-nav .main-menu > ul {
		display: flex;
		justify-content: space-between;
		width: calc( 100% - 140px );
		float: right;
	}
	.primary-nav .mobile-menu {
		display: none;
	}

	.flex-container-column, .off-canvas.flex-container-column.open {
		display: flex;
		flex-flow: column;
	}

	.close-menu {
		display: none;
	}

	.primary-nav .main-menu .left {
		display: block;
		width: 50%;
		padding-right: 2rem;
		float: left;
	}
	.primary-nav .main-menu .right {
		display: block;
		width:50%;
		padding-left: 2rem;
		float: right;
		border-left: 1px dashed map-get($eigen-colours, yellow);
	}
	.primary-nav .main-menu > ul {
		@include clearfix();
		clear:none;
		padding-top: 0;
		overflow: visible;	
	}

	.primary-nav ul li ul {
		padding-left: 0;
	}

	.primary-nav .drop-header {
		padding-left: 0;
		margin-top: 0;
	}

	.chevron {
		display: none;
	}
	.sub-navigation {
		display: none;
		position: absolute;
		top: 100%;
		left:0;
		padding: 2rem;
		background-color: map-get($eigen-colours, navy);
		color: map-get($eigen-colours, white);
		width: 100%;
		z-index: 10000;
	}
	.primary-nav .main-menu > ul > li {
		padding:0 1rem;
		text-align: center;
		margin-top: 0;
		line-height: 67px;
		flex-grow: 1;

		a {
			display: block;
			width:100%;
			height:100%;
		}
	}

	.primary-nav .main-menu > ul > li li {
		text-align: left;
	}

	.primary-nav .main-menu > ul > li:hover {
		height: 67px;
		border-bottom: 3px solid map-get($eigen-colours, yellow);

		.sub-navigation {
			display: block;
		}
	}

	.has-bg-image .primary-nav .main-menu a, .has-bg-image .primary-nav:hover .main-menu-wrapper .main-menu .sub-navigation a {
		color: map-get($eigen-colours, white);
		transition: color .3s ease-in-out;
	}

	.has-bg-image .primary-nav .main-menu-wrapper.stick .main-menu a, .has-bg-image .primary-nav:hover .main-menu a, .primary-nav .main-menu a {
		color: map-get($eigen-colours, dark-grey);

		.sub-navigation a {
			color: white;
		}

		.sub-navigation a:hover {
			color: map-get($eigen-colours, blue);
		}
	}

	.primary-nav .main-menu > ul ul a {
		color: map-get($eigen-colours, white);
	}
}